/* Box Number List */
ol.box-number {
  counter-reset: li;
  padding-left: 3rem;
}

ol.box-number li {
  list-style: none;
  padding-bottom: 0.75rem;
  position: relative;
}

ol.box-number li:before {
  background-color: #006195;
  border-radius: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #FFFFFF;
  content: counter(li);
  counter-increment: li;
  font-size: 1rem;
  font-weight: bold;
  left: -3rem;
  margin: 4px 8px 0 0;
  padding: 4px;
  position: absolute;
  text-align: center;
  top: -8px;
  width: 2rem;
}

ol.box-number ol {
  counter-reset: list;
  padding-top: 1rem;
  margin-left: 1.875rem;
}

ol.box-number ol li:before {
  content: counter(list, lower-alpha);
  counter-increment: list;
}

ol.box-number ul {
  margin-left: 1.875rem;
  padding-top: 1rem;
}

ol.box-number ul li:before {
  background-color: transparent;
  color: #000000;
  content: "\2022";
  counter-increment: list;
  font-size: 1.5rem;
  left: -3rem;
  margin-top: 0;
}

/* Icon List */
ul.list-icon {
  list-style-type: none;
}

ul.list-icon li {
  padding-bottom: 0.75rem;
  position: relative;
}

ul.list-icon span:before {
  font-size: 2rem;
  margin-left: -3rem;
  position: absolute;
  top: -6px;
}

ul.list-icon .fa-check {
  color: #46A660;
}

ul.list-icon .fa-times {
  color: #E87511;
}

/* Box Alpha List */
ol.box-alpha {
  counter-reset: letter;
  padding-left: 3rem;
}

ol.box-alpha li {
  list-style: none;
  padding-bottom: 0.75rem;
  position: relative;
}

ol.box-alpha li:before {
  background-color: #000000;
  border-radius: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #FFFFFF;
  content: counter(letter, lower-alpha);
  counter-increment: letter;
  font-size: 1rem;
  font-weight: bold;
  left: -3rem;
  margin: 4px 8px 0 0;
  padding: 4px;
  position: absolute;
  text-align: center;
  top: -8px;
  width: 2rem;
}

ol.box-alpha ol {
  counter-reset: list;
  padding-top: 1rem;
  margin-left: 1.875rem;
}

ol.box-alpha ol li:before {
  content: counter(list, lower-roman);
  counter-increment: list;
}

ol.box-alpha ul {
  padding-top: 1rem;
  margin-left: 1.875rem;
}

ol.box-alpha ul li:before {
  background-color: transparent;
  color: #000000;
  content: "\2022";
  counter-increment: list;
  font-size: 1.5rem;
  left: -3rem;
  margin-top: 0;
}

/* Large Number List - for Learning Objectives */
ol.large-number {
    margin-left: 0;
    padding-top: 10px;
    padding-left: 4.5rem;
    counter-reset: item;

    li {
        list-style: none;
        margin-bottom: 0.5rem;
        position: relative;
        margin-right: unset !important;

        &:before {
            content: counter(item) ". ";
            counter-increment: item;
            color: var(--sakai-primary-color-1);
            left: -2.25rem;
            margin: 8px 8px 0 0;
            position: absolute;
            top: -0.9375rem;
            font-size: 1.75rem;
            line-height: 2.75rem;
            font-weight: bold;
            margin-right: 0.9375rem;
          }
    }

    ol, ul {
        counter-reset: list;
        padding-top: 1rem;
        margin-left: 1.875rem;

        li:before {
            color: #000000;
            counter-increment: list;
            left: -40px;
            top: -3px;
            font-size: 1.5rem;
            line-height: 1rem;
            font-weight: 400;
        }
    }

    ol {
        li:before {
            content: counter(list, lower-roman) ". ";
        }
        ul li:before {
            content: "\2022";
        }
    }

    ul {
        li:before {
            content: "\2022";
        }
        ol li:before {
            content: counter(list, lower-roman) ". ";
        }
    }
}

@media screen and (max-width: 768px) {
    ol.large-number {
        li {
            padding-bottom: 0.6rem;
            margin-bottom: 0rem;
        }

        & > li:before {
            font-size: 1.5rem;
            left: -1.7rem;
            top: -1rem;
            margin: 6px 8px 0 0;
        }
    }
}

